import React, { useState, forwardRef, useEffect } from "react";
import { Dialog, AppBar, Toolbar, IconButton, Typography,
    Button, Slide, Container, Checkbox, TextField, List, ListItem, unstable_ClassNameGenerator } from "@mui/material";
import  CloseIcon  from "@mui/icons-material/Close";

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const GroupsForm = ({ open, dialogHandleClose, dialogHandleSave, dialogHandleDelete, rowData }) => {
    const [formData, setFormData] = useState({});
    const [departmentErrorString, setDepartmentErrorString] = useState("");

    useEffect(() =>{
        setFormData({
            id: rowData?.id || null,
            department: rowData?.department || "",
            status: rowData?.status || false,
        });
    },[rowData]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));
    };

    const handleErrorOnSave = (errors) => {

        let department_str_error = "";

        for( const key in errors){
            if(errors[key].field == 'department'){
                department_str_error += ` ${errors[key].error_message}`
            }

        }
        setDepartmentErrorString(department_str_error);

    };
    return (
        <Dialog
            fullScreen
            open={open}
            onClose={dialogHandleClose}
            TransitionComponent={Transition}
        >
            <AppBar sx={{ position: 'relative', backgroundColor: "#00755f" }}>
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={dialogHandleClose}
                        aria-label="close"
                        >
                        <CloseIcon />
                    </IconButton>

                    <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                    { rowData ? "Edit Group Settings": "Add Group Settings" }
                    </Typography>

                    {rowData ? (
                        <Button autoFocus color="inherit" onClick={()=>dialogHandleDelete(formData, handleErrorOnSave)}>
                        Delete
                        </Button>
                    ):null}

                </Toolbar>

            </AppBar>
            <Container maxWidth="sm"
                style={{ height: "100vh", display: "flex", justifyContent: "center", marginTop: 50}}
                >
                <List sx={{
                    height: "100vh",
                    display: "grid",
                    width: 600,
                    maxWidth: "sm",
                    bgcolor: 'background.paper',
                    alignContent: "baseline"
                    }}>
                    <ListItem>
                        <TextField id="outlined-basic" label="Department" variant="outlined" name="department" value={formData?.department}
                        onChange={handleChange}
                        sx = {{
                            width: "100%"
                        }} />
                    </ListItem>
                    { departmentErrorString && (
                        <ListItem>
                            <Typography variant="subtitle2" color="error" sx={{ width: "100%"}}>
                                {departmentErrorString}
                            </Typography>
                        </ListItem>
                    )}
                    {rowData ? (
                    <ListItem>
                        <Checkbox
                            onChange={()=>{
                                const { name, checked } = event.target;
                                setFormData((prevFormData) => ({
                                ...prevFormData,
                                [name]: checked,
                                }));
                            }}
                            checked={formData?.status || false}
                            name="status"
                            sx={{ '& .MuiSvgIcon-root': { fontSize: 28 } }}
                            /> Status Active
                    </ListItem>
                    ):null}


                    <ListItem sx={{
                            display: "block",
                            marginTop: '50px',
                            textAlign: "right",
                        }}>

                        <Button autoFocus variant="contained" onClick={()=>dialogHandleSave(formData, handleErrorOnSave)}>
                        save
                        </Button>

                    </ListItem>

                </List>




            </Container>

        </Dialog>
    );
}
export default GroupsForm;